import React, { Component } from 'react';

export function TurnContent() {
    return(
        <div className="contentTool">
            <div>6</div>
            <div>7</div>
            <div>8</div>
            <div>9</div>
            <div className="noneBorder">10</div>
        </div>
    );
}

export default class Turn extends Component{

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem('turn');
    }

    render(){
        let temp = 'toolsItem turn';
        if(this.props.tools.currentItem === 'turn')
            temp += ' active';
        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
            </div>
        );
    }
}